/*
 *   Name: BizAgi Mashup Main Controller
 *   Author: Ricardo Prez
 *   Comments:
 *   -   This script will define a base class to handle workportal layouts for any device
 *   -   This layout must contain a menu and widget container placeholders
 */


// Defines the main controller
bizagi.mashup.controllers.render.extend("bizagi.mashup.controllers.render", {}, {
    init: function (mashupFacade, dataService, params) {
        var self = this;
        //Load templates
        self._super(mashupFacade, dataService, params);

        self.loadTemplates({
            "mashup-render-view": bizagi.getTemplate("bizagi.mashup.controller.tablet.render").concat("#mashup-render-view"),
            "mashup-render-layout": bizagi.getTemplate("bizagi.mashup.controller.tablet.render").concat("#mashup-render-layout"),
            "mashup-render-edition-view": bizagi.getTemplate("bizagi.mashup.controller.tablet.render").concat("#mashup-render-edition-view"),
            "mashup-info-message": bizagi.getTemplate("bizagi.mashup.controller.tablet.render").concat("#mashup-info-message"),
            "mashup-routing": bizagi.getTemplate("bizagi.mashup.controller.tablet.render").concat("#mashup-routing"),
            "mashup-async": bizagi.getTemplate("bizagi.mashup.controller.tablet.render").concat("#mashup-async"),
            useNewEngine: false
        });
    },

    postRender: function () {
        var self = this;
        self._super();
    }
});
