// Auto extend
$.Class.extend("bizagi.mashup.controller.navigation", {
    PRIORITY_UNDEFINED: -1,
    PRIORITY_PRIMARY: 1,
    PRIORITY_SECONDARY: 2,
    PRIORITY_LIST: ["next", "save", "validate", "nextWithoutValidations", "createnewcase", "search", "select", "add", ""]
}, {

    /**
     * Constructor
     * @param params
     */
    init: function (controller) {
        var self = this;
        self.controller = controller;
        self.view = self.controller.view;
        self.$pane = self.view.wrapper.closest("[data-role='pane']");
        self.pane = self.$pane.data("kendoMobilePane");
        self.navBar = $(".km-navbar", self.view.header).data("kendoMobileNavBar");

        //Will contains all the dinamic views created
        self.containersQueue = [];
    },

    resetParams: function () {
        var self = this;
        if (typeof self.navBar !== "undefined") {
            self.navBar.title("");
            $(".bz-render-dynamic", self.navBar.element).hide();
        }
    },

    /**
     * creates a container to paint a new render form
     */
    createRenderContainer: function (params) {
        var self = this;
        params = params || {};
        params.view = params.view || {};
        var now = $.now();
        var id = "render-edition-" + now;
        var zoom = typeof params.view.zoom !== "undefined" ? (Boolean(params.view.zoom)) : false;
        var useNativeScrolling = typeof params.view.useNativeScrolling !== "undefined" ? (Boolean(params.view.useNativeScrolling)) : true;

        var $view = $.kendoTmpls(self.controller.getTemplate("mashup-render-edition-view"), {
            "viewId": id,
            "title": params.title || ""
        });

        self.setContainerParams(params);

        self.$pane.append($view);
        var viewItem = new kendo.mobile.ui.View($view,
            {
                "zoom": zoom,
                "useNativeScrolling": useNativeScrolling,
                "destroy": function () {
                    var tmplView = this;
                    var el = self.containersQueue.find(function (el) {
                        return "#".concat(el.id) === tmplView.id;
                    });
                    var index = self.containersQueue.indexOf(el);
                    self.containersQueue.splice(index, 1);
                    self.controller.navigate(el.previousViewId, "", true);
                    this.element.remove();

                    //IMPORTANT
                    //This block of code helps to fix an issue with mashups inside iframe for Sharepoint Projects.
                    //After the user opens an formlink and then close it, the scroll from the parent container is affected
                    //and does not let scroll the page again. Moving the scroll helps to readjust the focus
                    var view = self.pane.view();
                    var scrollElement = view.scroller.scrollElement;

                    if (scrollElement) {
                        var currentScrollY = scrollElement.scrollTop();
                        $(scrollElement).scrollTop(currentScrollY - 1).scrollTop(currentScrollY + 1);
                    }
                }
            }
        );

        var returnObject = {
            id: id,
            element: viewItem.contentElement(),
            destroy: function () {
                viewItem.destroy();
            },
            setNavigationButtons: function (form) {
                self.setNavigationButtons(form, this.id);
            }
        };

        $(".bz-render-back", viewItem.header).bind("click", function () {
            returnObject.element.trigger("close", {});
            viewItem.destroy();
        });

        self.containersQueue.push($.extend(returnObject, {view: viewItem}));

        return returnObject;
    },

    navigate: function (id) {
        var self = this;
        var view = self.containersQueue.find(function (el) {
            return el.id === id;
        });

        if (typeof view !== "undefined") {
            view.previousViewId = bizagi.kendoMobileApplication.view().id;
            self.controller.navigate(view.id, "", true);
        }
    },

    setNavigationButtons: function (form) {
        var self = this;
        var buttons,
            hasButtons = false,
            properties = {};

        if (form && form.buttons && form.buttons.length > 0) {
            buttons = form.buttons;
            hasButtons = true;
            properties = form.properties;
        }

        self.changeButtonVisibilityByContainer(form);

        if (hasButtons && typeof self.navBar !== "undefined") {
            var dynamic = (properties.buttons || buttons);

            self.initFormButton(form, dynamic);
            /*$(".bz-render-dynamic", self.navBar.element).show().actionSheet({
                setDataToShow: function () {
                    var buttonsToShow = [];
                    for (var i = 0; i < dynamic.length; i++) {
                        dynamic[i].guid = dynamic[i].ordinal;
                        dynamic[i].displayName = dynamic[i].caption;

                        buttonsToShow.push(dynamic[i]);
                    }
                    return buttonsToShow;
                },
                actionClicked: function (action) {
                    // Default back/close if not ordinal
                    var buttonProperties = form.buttons[action.guid];
                    form.processButton(buttonProperties);
                }
            });*/
        }
    },

    modalViewDidAppear: function () {
    },

    modalViewDidDisappear: function () {
    },

    setContainerParams: function (params) {
    },

    initFormButton: function (form, dynamic) {
        var self = this;
        var buttonsToShow = dynamic.filter(function (item) {
            return (!["back", "cancel"].includes(item.action) && bizagi.util.parseBoolean(item.visible) !== false);
        }).map(function (item) {
            return {
                id: item.id,
                action: item.action,
                additional: false,
                label: item.caption,
                ordinal: item.ordinal,
                priority: self.Class.PRIORITY_SECONDARY,
                visible: bizagi.util.parseBoolean(item.visible) !== false,
                ranked: false
            };
        });

        if (buttonsToShow.length > 0) {
            $(form.container).formButtons({
                buttons: self.getListWithPriority(buttonsToShow),
                click: function (element, params) {
                    self.processButton(params.ordinal, form);
                }
            });
        }
    },

    changeButtonVisibilityByContainer: function (form) {
        if (form && form.container) {
            $(".ui-bizagi-container-children-form", form.container).addClass("ui-bizagi-form-no-buttons");
            $(".ui-bizagi-button-container, .ui-bizagi-container-button-edit", form.container).hide();
        }
    },

    getListWithPriority: function (buttonsToShow) {
        var self = this;
        var list = bizagi.clone(buttonsToShow);
        var counter = 0;

        var indexNext = self.findPriorityAction(list, 0);
        if (indexNext !== self.Class.PRIORITY_UNDEFINED) {
            list[indexNext].priority = self.Class.PRIORITY_PRIMARY;
            list[indexNext].ranked = true;
            counter++;
        }

        // Only has one action
        if (list.length === 1) return list;

        var indexSave = self.findPriorityAction(list, 0);
        if (indexSave !== self.Class.PRIORITY_UNDEFINED) {
            list[indexSave].priority = self.Class.PRIORITY_SECONDARY;
            list[indexSave].ranked = true;
            counter++;
        }

        list.forEach(function (item) {
            if (!item.ranked) {
                item.priority = counter++;
                item.ranked = true;
                item.additional = true;

            }
        });

        // The list was ordered
        bizagi.util.sortJSON(list, "priority", "desc");

        return list;
    },

    findPriorityAction: function (list, index) {
        var actionsList = this.Class.PRIORITY_LIST;

        var indexNext = list.findIndex(function (item) {
            return (item.action === actionsList[index] && !item.ranked);
        });

        if (indexNext !== this.Class.PRIORITY_UNDEFINED) return indexNext;
        return this.findPriorityAction(list, ++index);
    },

    /**
     * process the button for principal form or form whit callback in form-edition
     * @param button
     * @param form
     */
    processButton: function (ordinal, form) {
        var button = form.buttons.find(function (btn) {
            return btn.ordinal === ordinal;
        });

        if (button.callback) {
            button.callback();
        } else {
            form.processButton(button);
        }
    },



});