/*
 *   Name: BizAgi Workportal Main Controller
 *   Author: Ricardo Perez
 *   Comments:
 *   -   This script will define a base class to handle workportal layouts for any device
 *   -   This layout must contain a menu and widget container placeholders
 */


// Defines the main controller
bizagi.mashup.controllers.controller.extend("bizagi.mashup.controllers.render", {}, {

    init: function(mashupFacade, dataService, params) {
        var self = this;
        self._super(mashupFacade, dataService, params);

        if (bizagi.hasOwnProperty("kendo")) {
            this.templateService = new bizagi.kendo.templates.services.service(bizagi.localization);
        } else {
            this.templateService = new bizagi.templates.services.service(bizagi.localization)
        }
    },

/*    loadTemplate: function(template, templateDestination) {
        var self = this;

        // Go fetch the template
        return self.templateService.getTemplate(templateDestination, template)
            .done(function (resolvedTemplate) {
                self.templates[template] = resolvedTemplate;
            });
    },*/

    renderContent: function () {
        var self = this;
        var template = self.getTemplate("mashup-render-view");
        // Render content
        return $.kendoTmpls(template, {});
    },

    postRender: function(){
        var self = this;

        var template = self.getTemplate("mashup-render-layout");
        self.content.prepend($.kendoTmpls(template, {}));


        self.view = new kendo.mobile.ui.View(self.content,
            {
                "useNativeScrolling": true
            }
        );

        if (!self.creationParams.layoutHeader){
            self.view.header.hide();
        }

        self.navigation = new bizagi.mashup.controller.navigation(self);
        self.async = new bizagi.mashup.controllers.async(self);
        self.configureHandlers();
    },

    /**
     * Configures all handlers and subscribers for this controller
     *
     */
    configureHandlers: function () {
        var self = this;
        self.subscribe("renderCase", function (e, params) {
            //First tell to navigator, to reset params
            self.navigation.resetParams();

            // Check if the required params are filled
            if (bizagi.util.isEmpty(params.idCase)) {
                bizagi.log("Se requiere el id del caso 'idCase' para poder renderizar la forma");
                return null;
            }

            if (!bizagi.util.isEmpty(params.summaryForm)) {
                self.renderForm(params);
                return null;
            }

            // Check if the required params are filled
            if (bizagi.util.isEmpty(params.idWorkitem)) {
                var routingParameters = {};
                routingParameters.idCase = params.idCase;
                if(params.summaryForm){
                    routingParameters.summaryForm = true;
                }
                self.executeAction("routing", routingParameters);
                return null;
            }

            self.renderForm(params);
        });

        self.subscribe("refreshProxyPrefix", function (e, params) {
            if (typeof self.rendering !== "undefined") {
                self.rendering.dataService.serviceLocator.setProxyPrefix(params);
            }
        });

        $(".bz-render-back", self.view.header).bind("click", function () {
            self.view.contentElement().empty();
            self.mashupFacade.close();
        });

        self.subscribe("toggleHeader", function (e, params) {
            if (typeof params.showHeader !== "undefined"){
                if (params.showHeader) {
                    self.view.header.show();
                }
                else {
                    self.view.header.hide();
                }
            }
            else{
                self.view.header.toggle();
            }
        });
    },


    /**
     * Shows complex gateway inside a modal dialog
     * @param data
     * @returns {bizagi.dialog|dialog|bizagi.rendering.dialog.form}
     */
    renderRoutingDialog: function(params){
        var self = this;
        //show Loading

        var routing = new bizagi.mashup.controllers.routing(self.dataService, self.mashupFacade,
            {
                routingTmpl: self.getTemplate("mashup-routing")
            }
        );

        routing.render(params.data).done(function () {
            //hide Loading
        });
    },

    /**
     * Defines a method to render the form
     * @param params
     */
    renderForm: function(params) {
        var self = this;
        var rfParams = params;
        var renderContainer = self.view.contentElement();
        renderContainer.empty();

        if (!params.withOutGlobalForm) {
            if (typeof self.rendering === "undefined") {
                var proxyPrefix = (typeof (self.dataService.serviceLocator.proxyPrefix) !== "undefined")
                    ? self.dataService.serviceLocator.proxyPrefix : "";

                var paramsFacade = { "proxyPrefix": proxyPrefix, device: bizagi.util.detectDevice() };

                // Load render page
                self.rendering = new bizagi.rendering.facade(paramsFacade);

                // Attach handler to render container to subscribe for routing events
                renderContainer.bind("routing", function (context, triggerparams) {
                    // Executes routing action
                    var parameters = {
                        idCase: rfParams.idCase,
                        fromTask: rfParams.fromTask || rfParams.idTask,
                        fromWorkItemId: rfParams.fromWorkItemId || rfParams.idWorkitem,
                        isOfflineForm: rfParams.isOfflineForm || false,
                        formsRenderVersion: rfParams.formsRenderVersion
                    };

                    parameters = $.extend(parameters, triggerparams);
                    self.executeAction("routing", parameters);
                });

                //Binds rendering error form
                renderContainer.bind("errorform", function (error) {
                    //trigger error form to notify the facade
                    self.publish("error", error);
                });

                self.rendering.subscribe("renderActionButton", function (e, data) {
                    self.publish("renderActionButton", {
                        actions: data.actions,
                        data: {
                            idCase: rfParams.idCase,
                            idWorkitem: rfParams.idWorkitem,
                            idTask: rfParams.idTask,
                            idWorkflow: rfParams.idWorkflow
                        }
                    });
                });

                /* Data navigation communication*/
                this.rendering.subscribe("dataNavigation", function (e, params) {
                    self.publish("dataNavigation", params);
                   /* var etController = new bizagi.mashup.controllers.entityTemplates(self.mashupFacade, self.dataService, {canvas : self.canvas, navigation: self.navigation, data: params});
                    etController.render({title: params.data.displayName});*/
                });
            }

            // Executes rendering into render container
            bizagi.loading.start(); //Init loading
            $.when(self.rendering.execute(
                $.extend(params, {
                    canvas: renderContainer,
                    navigation: self.navigation
                })
            )).then(function () {
                //this returns a form, Once the form is rendered you can bind events to the form
            }).fail(function (error) {
                //self.publish("error", error);
            }).always(function () {
                bizagi.loading.stop(); //Whatever happens remove loading
            });
        }
        else {
            var infoTemplate = self.getTemplate("mashup-info-message");
            var message = params.messageForm || self.resources.getResource("render-without-globalform");
            var isOfflineForm = false;

            if (typeof (self.params) !== "undefined") {
                isOfflineForm = typeof (self.params.isOfflineForm) !== "undefined"
                    && bizagi.util.parseBoolean(self.params.isOfflineForm);
            }

            if (isOfflineForm) {
                message = bizagi.util.getMessageFromNetworkState(self.dataService.online);
            }

            var infoHtml = $.kendoTmpls(infoTemplate, {
                message: message
            });
            // Remove loading icon from summary container
            infoHtml.appendTo(renderContainer);

            $(".ui-bizagi-info-message-button", renderContainer).click(function () {
                self.view.contentElement().empty();
                self.mashupFacade.close();
            });
        }

        var navbar = $(".km-navbar", self.view.header).data("kendoMobileNavBar");
        if (typeof navbar !== "undefined") {
            var title = typeof params.displayName !== "undefined" ? params.displayName : (params.radNumber || params.idCase);
            navbar.title(title);
        }

        //Call the evento to paint summaryCase
        self.publish('renderSummaryCase', params);
        // Keep reference to rendering facade
        //self.renderingFacade = rendering;
    }
});
