/**
 *   Name: BizAgi Mashups SDK
 *   Author: Ricardo Perez
 *   Comments:
 *   -   This script will process a mashups page using a device factory to use the adequate rendering
 *       to create the layout
 */

/**
 * Creates a new facade object to interact with all Bizagi's SDK capabilities.
 * @module facade
 * @class bizagi.mashup.facade
 * @summary
 * Creates an interaction facade instance 'new bizagi.mashup.facade(config)'
 * @param {object}   config                         Object configuration parameters.
 * @param {object}   [config.authentication={}]     Object for authentication configuration parameters.
 * @param {string}   [config.authentication.type="OAuth"]    Currently supported: "OAuth" | "Bizagi".
 * @param {callback} [config.authentication.refreshTokenCallback] Function callable on every refresh token needed for the App, just optional for
 * Bizagi's authentication, in OAUth authentication is required in order to keep synchronized the access token.
 * @param {boolean} [config.authentication.autologin=true] Only will have effect for OAuth authentication, and indicates the Bizagi's SDK will try to
 * login the user again when the se session is lost.
 * @param {callback} [config.onError]               Event callback indicating errors in the App, (e) shows the detailed error.
 * @param {callback} [config.onClose]               Event callback indicating the user wants to go back (from the internal header).
 * @param {callback} [config.onRenderAction]        Event callback indicating actions performed in the form.
 *      actions ["validate, "refresh", "submitOnChange", "next", "routing", "release"];
 *      {@linkcode validate->} Indicating the form performed the modeled validations, for instance:  required fields, valid ranges,.. etc.
 *      {@linkcode refresh->} Indicating the form performed a repaint.
 *      {@linkcode submitOnChange->} Indicating the form did save the new data.
 *      {@linkcode next->} Indicating the form performed a request to advance the activity.
 *      {@linkcode routing->} Indicating the form try to perform a routing to the activity it must show.
 *      {@linkcode release->} the form indicates, that the current user has released the current activity.
 * @param {string}   [config.proxyPrefix='']       URL prefix for the render services.
 * @param {boolean}  [config.showHeader=false]     Flag to define if the component must shows the internal header at the beginning
 * @example
 *  const mashup = new bizagi.mashup.facade({
 *      proxyPrefix: "http://myhost.com/for/services",
 *      authentication: {
 *          type: "OAuth",
 *          refreshTokenCallback: function (successCallback, errorCallback) { ... }
 *      },
 *      onClose: function () {},
 *      onError: function(e){console.log("error", e);},
 *      onRenderAction: function(e){console.log("actions", e);},
 *      showHeader: true
 *  });
 *
 */
$.Class.extend("bizagi.mashup.facade", {},
    /** @lends bizagi.mashup.facade.prototype */
    {
        init: function (config) {
            // Set default config

            bizagi.loading.start();
            this.initialized = false;
            this.defaultParams = config || {};
            this.defaultParams.authentication = this.defaultParams.authentication || {};
            this.defaultParams.authentication.type = this.defaultParams.authentication.type || "OAuth";

            // Defines a device factory for all rendering
            this.deviceFactory = new bizagi.mashup.device.factory(this, config);

            // Creates a data service instance
            this.dataService = new bizagi.workportal.services.service(config);

            // Creates authentication instance
            switch (this.defaultParams.authentication.type) {
                case "OAuth":
                    this.authenticator = new bizagi.mashup.authentication.oauth(this, this.defaultParams.authentication);
                    break;
                case "Basic":
                    this.authenticator = new bizagi.mashup.authentication.basic(this, this.defaultParams.authentication);
                    break;
                default:
                    this.authenticator = new bizagi.mashup.authentication.bizagi(this, this.defaultParams.authentication);
                    break;
            }

            // Create instance of routing service
            this.dataService.routing = new bizagi.workportal.services.routing({dataService: this.dataService});

            this.execute().then(function () {
                bizagi.loading.stop();
            });
        },

        /**
         * Start point method to use in the main javascript pages.
         * This method will process everything and attach the HTML directly to the DOM.
         * @summary Start the execution.
         * @ignore
         * @private
         * @access private
         */
        execute: function () {
            var self = this;
            var canvas = self.defaultParams.canvas || $("body");

            if (self.initialized) {
                return self.ready();
            }
            // Creates ready deferred
            self.executionDeferred = new $.Deferred();

            // Create a workportal facade
            this.deviceFacade = self.deviceFactory.getMashupFacade(self.dataService);

            // Set callback when requests have been done
            return this.deviceFacade.then(function (mashupFacade) {
                // Creates workportal component
                self.mashupFacade = mashupFacade;

                // Get main controller
                self.controller = mashupFacade.getMainController({
                    canvas: canvas,
                    layoutHeader: self.defaultParams.showHeader || false
                });

                // Render the full content
                return self.controller.render().then(function () {

                    //Resolves promise
                    self.initialized = true;
                    self.executionDeferred.resolve();

                });
            });
        },

        /**
         * Returns the execution deferred to determine whether the component is ready or not.
         * @summary Whether or not the component is ready.
         * @public
         * @returns {object} Promise
         */
        ready: function () {
            return this.executionDeferred.promise();
        },

        /**
         * Navigates to the desired view identified by "id" with a "transition".
         * @summary View a specific element.
         * @private
         * @param {int|string} id       The viewId to navigate to, usually a render.
         * @param {string} transition   The animation type for transitions like: slide, zoom, overlay, ...
         * @ignore
         * @example
         * mashup.navigate(10001, 'slide:up');
         */
        navigate: function (id, transition) {
            var self = this;
            $.when(self.executionDeferred).then(function () {
                self.mashupFacade.navigate(id, transition, true);
            });
        },

        /**
         * Generates the visual elements for the requested case.
         * @summary View case.
         * @public
         * @param {object}  params              Object parameters
         * @param {int}     params.idCase       Case identifier.
         * @param {int}     params.idworkitem   WorkItem identifier.
         * @example
         * mashup.renderCase({"idCase": 110});
         * @example
         * mashup.renderCase({"idCase": 110, idWorkitem: 120});
         */
        renderCase: function (params) {
            var self = this;
            //if (typeof self.authenticator.getUser() !== "undefined") {
                self.ready().then(function () {
                    if (typeof self.controller !== "undefined") {
                        self.controller.publish("renderCase", params);
                        self.navigate("#render");

                        if (typeof self.defaultParams.onRender == "function") {
                            self.defaultParams.onRender.apply(this, arguments);
                        }

                    } else {
                        bizagi.log("Sorry we're not ready yet, please try again");
                    }
                });
            /*} else {
                if (typeof self.defaultParams.onError === "function") {
                    self.defaultParams.onError("You must log in to complete the action");
                }
            }*/
        },

        /**
         * By default Bizagi's SDK deliver a an integrated header in the rendered view
         * @summary Hides the default view header
         * @public
         * @example
         * mashup.hideInternalHeader();
         */
        hideInternalHeader: function () {
            var self = this;
            self.ready().then(function () {
                if (typeof self.controller !== "undefined") {
                    self.controller.publish("toggleHeader", {showHeader: false});
                } else {
                    bizagi.log("Sorry we're not ready yet, please try again");
                }
            });
        },

        /**
         * Ends the session of the authenticated user.
         * @summary close the user's session
         * @public
         * @example
         * mashup.logoutUser();
         */
        logoutUser: function () {
            var self = this;
            if (typeof self.authenticator.getUser() !== "undefined") {
                self.authenticator.logout();
            } else {
                console.log("there's not a user authenticated");
            }
        },

        /**
         * By default Bizagi's SDK deliver a an integrated header in the rendered view
         * @summary Shows the default view header.
         * @public
         * @example
         * mashup.showInternalHeader();
         */
        showInternalHeader: function () {
            var self = this;
            self.ready().then(function () {
                if (typeof self.controller !== "undefined") {
                    self.controller.publish("toggleHeader", {showHeader: true});
                } else {
                    bizagi.log("Sorry we're not ready yet, please try again");
                }
            });
        },

        /**
         * This method tries to get user access to Bizagi using the authentication credentials passed as arguments.
         * Depending on the actual method/type of authentication it will use different properties of the "auth" object.
         * @public
         * @summary
         * Authenticates the user
         * @param {object} auth                 Object parameters.
         *
         * @param {string} auth.user            Username or email (Bizagi Auth Only)
         * @param {string} auth.password        User's password (Bizagi Auth Only)
         * @param {string} auth.domain          User's domain when needed (Bizagi Auth Only)
         *
         * @param {string} auth.accessToken     User's OAuth access token (OAuth Only)

         * @returns {promise}                    Returns a promise with user's data.
         * @example <caption>Authentication sample returning user's data</caption>
         * mashup.authenticateUser({
         *  accessToken: access_token
         *  }).then(function(user){...});
         * // OR
         * mashup.authenticateUser({
         *  user: 'user1',
         *  password: '1234',
         *  domain: 'domain'
         * }).then(function(user){...});
         **/
        authenticateUser: function (auth) {
            return this.authenticator.authenticateUser(auth);
        },

        /**
         * This method allows to update the Bizagi's SDK access token, when client access token has been refreshed in order
         * to synchronize the access token on both sides
         * @public
         * @summary
         * Sets the authentication access token
         * @param {string} accessToken        User access token
         * @example
         * mashup.setAccessToken(access_token);
         **/
        setAccessToken: function (accessToken) {
            if (this.defaultParams.authentication.type == "OAuth") {
                this.authenticator.setAccessToken({accessToken: accessToken});
            } else {
                console.log("this call will not have any effect");
            }
        },

        /**
         * Shows a summary data for the last or actual case opened.
         * @summary Shows a summary case.
         * @public
         * @example
         * mashup.showSummaryCase();
         */
        showSummaryCase: function (params) {
            var self = this;
            params = params || {};

            if (typeof self.authenticator.getUser() !== "undefined") {
                self.ready().then(function () {
                    if (typeof self.controller !== "undefined") {
                        self.controller.publish("showSummary", params);
                    } else {
                        bizagi.log("No se ha ejecutado el mashup facade.");
                    }
                });
            } else {
                if (typeof self.defaultParams.onError === "function") {
                    self.defaultParams.onError("You must login to complete the action.");
                }
            }
        },

        /**
         * Creates a brand new case with the provided configuration, if server version is older then 11.0, call
         * method with the param idWFClass, for server version greater than or equals call the method with the param
         * idProcess.
         * @summary Create a new case.
         * @param {object}  params                 Object parameters.
         *
         * @param {int}     params.idWFClass       Id of the WFClass or process for (For server versions lower than 11.0).
         * @param {int}     params.idProcess       Id of the process (For server versions 11.0 and higher).
         * @example
         * mashup.createNewCase({"idWFClass": 1});
         * //OR
         * mashup.createNewCase({"idProcess": 1});
         */
        createNewCase: function (params) {
            var self = this;

            if (typeof self.authenticator.getUser() !== "undefined") {
                self.ready().then(function () {
                    if (self.controller) {
                        self.controller.publish("createNewCase", params);
                    } else {
                        bizagi.log("No se ha ejecutado el mashup facade.");
                    }
                });
            } else {
                if (typeof self.defaultParams.onError === "function") {
                    self.defaultParams.onError("You must login to complete the action.");
                }
            }
        },

        renderCaseSummary: function (params) {
            var self = this;
            params = params || {};

            self.ready().then(function () {
                if (typeof self.controller !== "undefined") {
                    self.controller.publish('renderSummaryCase', params);
                } else {
                    bizagi.log("No se ha ejecutado el mashup facade");
                }
            });
        }

    }
);
