bizagi.mashup.controllers.controller.extend("bizagi.mashup.actions.routing", {}, {

    /*
     *   Executes the action
     *   Could return a deferred
     */
    execute: function (params) {
        var self = this;
        self.params = params || {};
        /* New implementation of routing*/

        $.when(self.dataService.routing.getRoute(params)).done(function (route) {
            route = route || {};

            var isOfflineForm = false;
            if (typeof (self.params) != "undefined") {
                isOfflineForm = typeof (self.params.isOfflineForm) !== "undefined"
					&& bizagi.util.parseBoolean(self.params.isOfflineForm);
            }

            if (isOfflineForm) {
                route.moduleParams = $.extend(route.moduleParams,
                {
                    formsRenderVersion: self.params.formsRenderVersion,
                    isOfflineForm: isOfflineForm,
                    idCase: self.params.idCase,
                    idWorkitem: self.params.idWorkitem
                }) || {};
            } else {
                route.moduleParams = route.moduleParams || {};
            }

            switch (route.module) {
                case "activityform":
                    self.publish("renderCase", route.moduleParams);
                    break;
                case "oldrenderintegration":
                    self.publish("renderCase", route.moduleParams);
                    break;
                case "async":
                    self.publish("render-async", route.moduleParams);
                    break;
                case "routing":
                    var parameters = {
                        title: self.getResource("workportal-widget-routing-window-selector"),
                        onClose: route.moduleParams.onClose
                    };

                    if (self.isThereAsync(route.moduleParams.data)) {
                        self.asyncWidget(route.moduleParams);
                    } else {
                        self.publish("showDialog", {
                            widgetName: "routing",
                            data: route.moduleParams.data,
                            modalParameters: parameters,
                            onClose: parameters.onClose
                        });
                    }
                    break;
            }

        }).fail(function (error){
            self.publish("error", error);
        });
    },

    /*
     * Search for async workitems
     */
    isThereAsync: function (data) {
        var workitems = data.workItems, wi, index = 0;
        var length = workitems.length, result = false;
        if (length > 0) {
            for (; index < length; ) {
                wi = workitems[index++];
                if (wi.isAsynch === "true") {
                    result = result || [];
                    result.push(wi);
                }
            }
        }
        return result;
    },

    asyncWidget: function (params) {
        var self = this;
        params.widgetName = "async";
        self.publish("changeWidget", params);
    }
});

