/*
 *   Name: Bizagi CaseSummary
 *   Author: Bizagi Mobile Team
 *   Comments:
 *   -   This script will define a base class to handle workportal layouts for any device
 *   -   This layout must contain a menu and widget container placeholders
 */


// Defines the main controller
bizagi.mashup.controllers.controller.extend("bizagi.mashup.controllers.caseSummary", {}, {
    init: function (mashupFacade, dataService, params) {
        this._super(mashupFacade, dataService, params);

        if (bizagi.hasOwnProperty("kendo")) {
            this.templateService = new bizagi.kendo.templates.services.service(bizagi.localization);
        } else {
            this.templateService = new bizagi.templates.services.service(bizagi.localization)
        }

        this.initialized = false;
    },

    renderContent: function () {
        var self = this;
        var template = self.getTemplate("mashup-summary-view");
        // Render content
        var content = $.kendoTmpls(template);

        return content;
    },

    postRender: function () {
        var self = this;

        self.drawer = new kendo.mobile.ui.Drawer(self.content,
            {
                "swipeToOpen": false,
                "position": "rigth"
            }
        );
    },

    /**
     * Base event handlers
     */
    configureHandlers: function () {
        var self = this;

        self._super();

        self.subscribe("showSummary", function () {
            if (typeof self.drawer !== "undefined" && self.initialized) {
                self.drawer.show();
            } else {
                bizagi.log("The summary case controller has not been initialized");
            }
        });

        self.drawer.bind("show", function () {
            $(".bz-header_select", self.canvas).trigger("resizeHeader");
        });

        /* istanbul ignore next */
        $("#case-summary", self.canvas).delegate(".summaryLink", "click", function (e) {

            e.preventDefault();

            self.routingExecute($(this));

            $($(this), self.canvas).remove();
            self.cache = {};
        });
    },

    /**
     * Renders the tabs component of the webpart
     * @param args
     * @returns {*}
     */
    renderCurrentTab: function (args) {
        var self = this;
        var htmlContent = "";
        var defer = new $.Deferred();
        var params = args.params || self.params || {};

        self.cache = self.cache || {};
        self.data = self.data || {};

        switch (args.currentTab) {
            case "#ui-bizagi-tab-assignees":
                if (typeof (self.cache["assignees"]) === "undefined") {
                    $.when(
                        self.dataService.summaryAssigness({
                            idCase: params.idCase
                        })).done(function (assignees) {
                            htmlContent = $.kendoTmpls(bizagi.util.trim(self.getTemplate("mashup-summary-assigness")), assignees);
                            self.cache["assignees"] = true;

                            $(args.currentTab, self.content).empty();
                            $(args.currentTab, self.canvas).html(htmlContent);

                            bizagi.loading.stop();
                            defer.resolve();
                        });
                } else {
                    bizagi.loading.stop();
                    defer.resolve();
                }
                break;
            case "#ui-bizagi-tab-comments":
                if (typeof (self.cache["comments"]) === "undefined") {
                    $.when(
                        self.dataService.getComments({
                            idCase: params.radNumber || params.idCase,
                            idLastComment: self.Class.COMMENT_SIZE
                        })).done(function (comments) {
                            comments["showComments"] = comments.totalRecords > 0 ? true : false;
                            comments["timeAgo"] = bizagi.getTimeAgo;
                            comments["getUserName"] = function (idUser) {
                                var users = comments.users || [];
                                var name = "";
                                $.each(users, function (key, value) {
                                    if (users[key]["Id"] == idUser) {
                                        name = value.DisplayName;
                                    }
                                });

                                return name;
                            };

                            htmlContent = $.kendoTmpls(bizagi.util.trim(self.getTemplate("mashup-summary-comments")), comments);
                            self.cache["comments"] = true;

                            $(args.currentTab, self.content).empty();
                            $(args.currentTab, self.canvas).html(htmlContent);

                            bizagi.loading.stop();
                            defer.resolve();
                        });
                } else {
                    bizagi.loading.stop();
                    defer.resolve();
                }

                break;
            case "#ui-bizagi-tab-subprocess":
                if (typeof (self.cache["subprocess"]) === "undefined") {
                    $.when(
                        self.dataService.summarySubProcess({
                            idCase: params.idCase
                        })).done(function (subprocess) {
                            htmlContent = $.kendoTmpls(bizagi.util.trim(self.getTemplate("mashup-summary-subprocess")), subprocess);
                            self.cache["subprocess"] = true;

                            $(args.currentTab, self.content).empty();
                            $(args.currentTab, self.canvas).html(htmlContent);

                            bizagi.loading.stop();
                            defer.resolve();
                        });
                } else {
                    bizagi.loading.stop();
                    defer.resolve();
                }
                break;
            case "#ui-bizagi-tab-events":
                if (typeof (self.cache["events"]) === "undefined") {
                    $.when(
                        self.dataService.summaryCaseEvents({
                            idCase: params.idCase
                        })).done(function (events) {
                            htmlContent = $.kendoTmpls(bizagi.util.trim(self.getTemplate("mashup-summary-events")), events);
                            self.cache["events"] = true;

                            $(args.currentTab, self.content).empty();
                            $(args.currentTab, self.canvas).html(htmlContent);

                            bizagi.loading.stop();
                            defer.resolve();
                        });
                } else {
                    bizagi.loading.stop();
                    defer.resolve();
                }
                break;
            case "#ui-bizagi-tab-activities":
                if (typeof (self.cache["activities"]) === "undefined") {
                    $.when(
                        self.dataService.summaryActivities({
                            data: self.data,
                            idWorkitem: params.idWorkitem
                        })).done(function (activities) {
                            activities["idCase"] = params.idCase;

                            htmlContent = $.kendoTmpls(bizagi.util.trim(self.getTemplate("mashup-summary-activities")), activities);
                            self.cache["activities"] = true;

                            $(args.currentTab, self.content).empty();
                            $(args.currentTab, self.canvas).html(htmlContent);

                            htmlContent.delegate(".summaryLink", "click", function (e) {
                                e.preventDefault();
                                self.routingExecute($(this));
                            });

                            bizagi.loading.stop();
                            defer.resolve();
                        });
                } else {
                    bizagi.loading.stop();
                    defer.resolve();
                }
                break;
            default:
                bizagi.loading.stop();
                defer.resolve();
                break;
        }
        return defer.promise();
    },
    /*
     *   Executes routing action
     */
    routingExecute: function (element) {
        var self = this;
        var defer = new $.Deferred();

        if (element) {
            var idCase = element.data("case") || self.params.idCase;
            var idWorkItem = element.data("workitem");
            var idTask = element.data("task");
            var displayName = element.data("displayname") || self.params.displayName;

            $(".km-content:visible").data("kendoMobileScroller").reset();
            $("#case-summary").data("kendoMobileDrawer").hide();


            self.publish("renderCase", {
                idCase: idCase,
                idWorkitem: idWorkItem,
                idTask: idTask,
                displayName: displayName
            });
            defer.resolve(true);

        } else {
            defer.resolve(false);
        }
        return defer.promise();
    },

    /*
     * Action release
     */
    release: function (params) {
        var self = this;
        var defered = new $.Deferred();
        var buttons = [
            { "label": self.getResource("workportal-widget-dialog-box-release-ok"), "action": "resolve" },
            { "label": self.getResource("workportal-widget-dialog-box-release-cancel") }
        ];

        $.when(bizagi.showConfirmationBox(self.getResource("workportal-widget-dialog-box-release"), self
                .getResource("render-actions-release"), "", buttons))
            .done(function () {

                bizagi.loading.start();

                $.when(self.dataService.releaseActivity({
                    idCase: params.idCase,
                    idWorkItem: params.idWorkitem
                })).done(function (data) {

                    var status = (data && data.status) ? data.status : "Error";

                    switch (status) {
                        case "Success":
                            //bizagi.kendoMobileApplication.navigate("homePortal"); //TODO:Review taskFeed
                            self.publish("renderActionButton", { actions: ["release", "routing"] });
                            self.publish("renderCase", {
                                action: "routing",
                                idCase: params.idCase,
                                idWorkitem: params.idWorkitem
                            });
                            //Show message on rubik
                            break;
                        case "ConfigurationError":
                            bizagi.showMessageBox(self
                                .getResource("workportal-widget-dialog-box-release-configuration-error-message")
                                .replace("{0}", params.idWorkitem), self
                                .getResource("workportal-widget-dialog-box-release-error"), "error", false);
                            break;
                        case "Error":
                            bizagi.showMessageBox(self.getResource("workportal-widget-dialog-box-release-error-message")
                                .replace("{0}", params.idWorkitem), self
                                .getResource("workportal-widget-dialog-box-release-error"), "error", false);
                            break;
                        default:
                            break;
                    }

                    bizagi.loading.stop();

                    defered.resolve();
                }).fail(function () {
                    var message = self.getResource("workportal-widget-dialog-box-release-error-message").replace("{0}",
                        params.idWorkitem);
                    bizagi.showMessageBox(message, self.getResource("workportal-widget-dialog-box-release-error"),
                        "error", false);
                    bizagi.loading.stop();
                    defered.reject();
                });
            });
        return defered.promise();
    }
});
