/**
 * Controlador de dialogos de bizagi, este controlador esta hecho especificamente para kendo, si se usa otro framework 
 * debe escribir una clase que sobreescriba esta o sobre escribir los metodos aqui consignados de nuevo.
 * Created by RicardoPD on 4/18/2016.
 */

bizagi.mashup.controllers.controller.extend("bizagi.mashup.controllers.dialog", {}, {
    init: function(mashupFacade, dataService, params) {
        var self = this;
        //Load templates
        self._super(mashupFacade, dataService, params);

        if (bizagi.hasOwnProperty("kendo")) {
            this.templateService = new bizagi.kendo.templates.services.service(bizagi.localization);
        } else {
            this.templateService = new bizagi.templates.services.service(bizagi.localization)
        }

        self.loadTemplates({
            "mashup-dialog": bizagi.getTemplate("bizagi.mashup.common.dialog").concat("#mashup-dialog"),
            useNewEngine: false
        });
    },

    /**
     * Render the Dialog
     * @param params
     */
    renderContent: function(params) {
        var self = this;
        var dialogTmpl = self.getTemplate("mashup-dialog");
        var now = $.now();
        self.id = "modal-" + now;
        var content = $.kendoTmpls(bizagi.util.trim(dialogTmpl), { id: self.id }).clone();
        content.html(params.content);
        return content;
    },

    postRender: function() {
        var self = this;

        self.content.kendoMobileModalView({
            close: function() {
                this.destroy();
                this.element.remove();
            },
            useNativeScrolling: true,
            modal: self.creationParams.modal || true
        });

        self.modalView = self.content.data("kendoMobileModalView");

        self.modalView.open();
    },

    close: function() {
        if (typeof this.modalView !== "undefined") {
            this.modalView.close();
        }
    }

});
