bizagi.mashup.controllers.controller.extend("bizagi.mashup.controllers.routing", {
}, {

    renderContent: function (params) {        
        var routingTmpl = this.creationParams.routingTmpl || "";
        return $.kendoTmpls(routingTmpl, params);
    },

    /*
     *   To be overriden in each device to apply layouts
     */
    postRender: function() {
        var self = this;
        var dialog = new bizagi.mashup.controllers.dialog(self.dataService, self.mashupFacade);

        dialog.render({ content: self.content }).done(function() {

            // Assing even style
            $("tr:nth-child(even)", self.content).addClass("event");

            $("#ui-bizagi-wp-app-routing-activity-wf .bz-wp-routing-action", self.content).on("click", function() {
                var item = $(this).closest(".bz-wp-routing-item").data("item");

                self.showWorkitem({
                    idCase: item.idCase || 0,
                    idWorkitem: item.idWorkItem || 0,
                    idTask: item.idTask || 0,
                    taskName: item.displayName || "",
                    radNumber: item.radNumber || 0
                });

                // close dialog
                dialog.close();
            });

        });
    },

    /*
     *   Shows the rendering widget
     */
    showWorkitem: function (params) {
        var self = this;
        // Shows render widget

        self.publish("renderCase", {
            idCase: params.idCase,
            idWorkitem: params.idWorkitem,
            idTask: params.idTask,
            taskName: params.taskName,
            radNumber: params.radNumber
        });
    }
});