$.Class.extend("bizagi.mashup.authentication", {}, {


    /**
     * Authentication class constructor
     * @param facade
     * @param params
     */
    init: function (facade, params) {
        this.facade = facade;
        this.dataService = facade.dataService;
        this.creationParams = params;
    },

    authenticateUser: function () {
    },
    
    logout: function () {
    },

    /**
     * fethc the current user information form bizagi platform 
     * @returns {*}
     */
    getCurrentUser: function () {
        var self = this;

        return self.dataService.getCurrentUser().then(
            function (currentUser) {
                // Sacar de este lugar cuando se adicione el security al currentUser
                currentUser.userProperties = {};
                var userProperty;
                var i = -1;

                while ((userProperty = currentUser.sUserProperties[++i])) {
                    currentUser.userProperties[userProperty.key] = userProperty.value;
                }

                self.currentUser = bizagi.currentUser = currentUser;
                
                return currentUser;
            });
    },

    getUser: function () {
        return this.currentUser;
    }
});
